import React from 'react';
import styles from './index.scss';
import classNames from 'classnames/bind';
import ModalWrapper from '../ModalWrapper';
import Button from '../../common/Button';
const cx = classNames.bind(styles);

const LoginModal = ({visible,  onCancel, password, onChange, onKeyPress, onLogin, error}) => {
    // &times; X .
    return(
    <>
        <ModalWrapper visible={visible}>
            <div className={cx('form')}>
                <div onClick={onCancel} className={cx('close')}>&times;</div>
                <div className={cx('title')}></div>
                <div className={cx('description')}>  </div>
                <input autoFocus type='password' placeholder='' value={password} onChange={onChange} onKeyPress={onKeyPress}/>
                {error && <div className = {cx('error')}> </div>}
                <div className={cx('login')} onClick = {onLogin}></div>
            </div>
        </ModalWrapper>
    </>
    );
};

export default LoginModal;