import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as editorActions from '../../store/modules/editor';
import EditorHeader from '../../compoenets/editor/EditorHeader';
import {withRouter} from 'react-router-dom';
import queryString from 'query-string';

/*
components/editor/EditorPane  EditorPane   
containers           .
  react-redux connect     EditorPaneContainer 
    .
*/

class EditorHeaderContainer extends Component{

    componentDidMount() {
        const {EditorActions, location} = this.props;
        EditorActions.initialize();

        const {id} =queryString.parse(location.search);
        if(id){
            EditorActions.getPost(id);
        }
    }

    handleGoBack = () => {
        const {history} = this.props;
        history.goBack();
    }

    handleSumit =  async() => {
        const { title, markdown, tags, EditorActions, history,location } = this.props;
        const {id} = queryString.parse(location.search);
        const post = {
            title,
            body: markdown,
            tags : tags === ''? []:[...new Set(tags.split(',').map(tag=>tag.trim()))] 
        };
        try {
            if(id){
                await EditorActions.editPost({id, ...post});
                history.push(`/post/${id}`);
                return;
            }
            await EditorActions.writePost(post);
            //routing   API
            history.push(`/post/${this.props.postId}`);
        }
        catch(e) {
            console.log(e);
        }
    }

    render(){
        const {handleGoBack,handleSumit} = this;
        const {id} = queryString.parse(this.props.location.search);
        return(
           <EditorHeader
                onGoBack={handleGoBack}
                onSubmit={handleSumit}
                isEdit = {id}
            />
        );
    }
}

/*          .*/
export default connect(
    (state) => ({
        title: state.editor.get('title'),
        markdown: state.editor.get('markdown'),
        tags: state.editor.get('tags'),
        postId: state.editor.get('postId')
    }),
    (dispatch) => ({
        EditorActions: bindActionCreators(editorActions, dispatch)
    })
)(withRouter(EditorHeaderContainer));

