/*    */
import React, {Component} from 'react';
import styles from './index.scss';
import classNames from 'classnames/bind';

import CodeMirror from 'codemirror';
import 'codemirror/mode/markdown/markdown'; //    
import 'codemirror/mode/javascript/javascript'; //     
import 'codemirror/mode/jsx/jsx';
import 'codemirror/mode/css/css';
import 'codemirror/mode/shell/shell';

import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/monokai.css';

const cx = classNames.bind(styles);

class EditorPane extends Component {
    editor = null;                  //  ref
    codeMirror = null;              // CodeMirror 
    cursor = null                   //   cursor 

    initializeEditor = () => {
        this.codeMirror = CodeMirror(this.editor, {
            mode: 'markdown',
            theme: 'monokai',
            lineNumbers: true,              //    
            lineWrapping: true              //      
        });
        this.codeMirror.on('change', this.handleChangeMarkdown);
        /*CodeMirror  change   handleChangeMarkdown */
    }

    componentDidMount(){
        this.initializeEditor();
    };

    handleChange = (e) => {
        const {onChangeInput} = this.props;
        const {value, name} = e.target;
        onChangeInput({name, value});
    }

    handleChangeMarkdown = (doc) => {
        const {onChangeInput} = this.props;
        this.cursor = doc.getCursor();         //  cursor  

        onChangeInput({
            name: 'markdown',
            value: doc.getValue()
        })
    }
    componentDidUpdate(prevProps, prevState) {
        if(prevProps.markdown !== this.props.markdown){
            const { codeMirror , cursor} = this;
            if(!codeMirror) return; //    
            codeMirror.setValue(this.props.markdown);
            if(!cursor) return;
            codeMirror.setCursor(cursor);
        }
    } 

    render(){
        const {handleChange} = this;
        const {tags, title} = this.props;
        return(
            <div className={cx('editor-pane')}>
                <input className={cx('title')} placeholder = ' ' name = 'title' value= {title} onChange={handleChange}/>
                <div className={cx('code-editor')} ref={ref=>this.editor=ref}></div>
                <div className = {cx('tags')}>
                    <div className={cx('description')}></div>
                    <input name = 'tags' placeholder = '  ( )' value = {tags} onChange={handleChange}/>
                </div>
            </div>
        );
    }
};

export default EditorPane;